@use 'base/mixins' as m;
@use 'base/variables' as v;

.dashboard{
    background-color: v.$girsClaro;
    height: 100vh;
    overflow: auto;
    @include m.tablet{
        display: flex;
       
        .principal{
            flex: 1;
            overflow-y: scroll;
        }

    }
   
    .contenido{
        padding: 2rem;
        .nombre-pagina{
            font-size: 2.8rem;
            text-align: center;
        }
    }
    .formulario{
        input:not([type="submit"]){
            background-color: v.$blanco;
        }
        input[type="submit"]{
            @include m.boton (v.$indigo,v.$blanco);
            padding: 1rem;
            text-transform: uppercase;
            font-weight: bold;
            font-size: 1.4rem;
        }
    }
}